<template>
  <div style="min-width: 1000px">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>策略授权</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <el-button type="primary" @click="openAdd">添加授权</el-button>
      </el-row>
      <el-table :data="userList" border stripe>
        <el-table-column type="index" label="序号"></el-table-column>
        <el-table-column label="策略名称" prop="strategyName" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column label="被授权主体"  :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <el-row v-if="scope.row.deviceInfo !== null">
                <span>
                  {{scope.row.deviceInfo.deviceName == null || scope.row.deviceInfo.deviceName == '' ? '-' : scope.row.deviceInfo.deviceName}}
              </span>
            </el-row>
            <el-row v-else-if="scope.row.roleInfo !== null">
              <span>
              {{scope.row.roleInfo.roleCode == null || scope.row.roleInfo.roleCode == '' ? '-' : scope.row.roleInfo.roleCode}}
              </span>
            </el-row>
            <el-row v-else-if="scope.row.groupInfo !== null">
              <span>
              {{scope.row.groupInfo.groupName == null || scope.row.groupInfo.groupName == '' ? '-' : scope.row.groupInfo.groupName}}
              </span>
            </el-row>
            <el-row v-else-if="scope.row.appUserInfo !== null">
              <span>
              {{scope.row.appUserInfo.appUserName == null || scope.row.appUserInfo.appUserName == '' ? '-' : scope.row.appUserInfo.appUserName}}
              </span>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column label="主体类型">
          <template slot-scope="scope">
            <span v-if="scope.row.deviceInfo !== null">终端用户</span>
            <span v-else-if="scope.row.roleInfo !== null">角色</span>
            <span v-else-if="scope.row.groupInfo !== null">分组</span>
            <span v-else-if="scope.row.appUserInfo !== null">应用用户</span>
          </template>
        </el-table-column>
        <el-table-column label="创建时间" prop="createTime" width="200px"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" type="danger"  @click="deleById(scope.row.strategyAuthId)">取消授权</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5,10,15,20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <el-dialog title="添加授权" :visible.sync="addDialogVisible" width="755px" @close="addDialogClosed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="130px">
        <el-form-item label="被授权主体类型:" prop="authType">
          <el-radio-group v-model.trim="addForm.authType" @change="changeAuthType">
            <el-radio :label="1">终端用户</el-radio>
            <el-radio :label="4">应用用户</el-radio>
            <el-radio :label="2">角色</el-radio>
            <el-radio :label="3">分组</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="被授权主体:" prop="valueUser" v-if="addForm.authType === 1">
          <el-select ref="inputValue"
                     v-model="addForm.valueUser"
                     multiple
                     filterable
                     remote
                     reserve-keyword
                     placeholder="请输入终端用户"
                     :remote-method="remoteMethod"
                     :loading="loading">
            <el-option
              v-for="item in optionsUser"
              :key="item.deviceId"
              :label="item.deviceName"
              :value="item.deviceId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="被授权主体:" prop="valueCode" v-else-if="addForm.authType === 2">
          <el-select ref="inputValue2"
                     v-model="addForm.valueCode"
                     multiple
                     filterable
                     remote
                     reserve-keyword
                     placeholder="请输入角色"
                     :remote-method="remoteMethod2"
                     :loading="loading">
            <el-option
              v-for="item in optionsCode"
              :key="item.roleId"
              :label="item.roleCode"
              :value="item.roleId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="被授权主体:" prop="valueGroup" v-else-if="addForm.authType === 3">
          <el-select ref="inputValue3"
                     v-model="addForm.valueGroup"
                     multiple
                     filterable
                     remote
                     reserve-keyword
                     placeholder="请输入分组"
                     :remote-method="remoteMethod3"
                     :loading="loading">
            <el-option
              v-for="item in optionsGroup"
              :key="item.groupId"
              :label="item.groupName"
              :value="item.groupId">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="被授权主体:" prop="valueAppUser" v-if="addForm.authType === 4">
          <el-select ref="inputValue"
                     v-model="addForm.valueAppUser"
                     multiple
                     filterable
                     remote
                     reserve-keyword
                     placeholder="请输入应用用户"
                     :remote-method="remoteMethod4"
                     :loading="loading">
            <el-option
              v-for="item in optionsAppUser"
              :key="item.userId"
              :label="item.userName"
              :value="item.userId">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div style="display: flex">
        <span class="supSpan">选择策略:</span>
        <el-transfer v-model="checked" :data="transferData" :titles="['策略', '策略名称']"></el-transfer>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary"  v-preventReClick="1000" @click="submitAdd">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        userList: [],
        queryInfo: {
          pagenum: 1,
          pagesize: 10
        },
        total: 0,
        isShow: false,
        addDialogVisible: false,
        queryValue: '',
        optionsUser: [],
        optionsCode: [],
        optionsGroup: [],
        optionsAppUser: [],
        loading: false,
        addForm: {
          authType: 1,
          valueUser: [],
          valueCode: [],
          valueGroup: [],
          valueAppUser:[]
        },
        addFormRules: {
          authType: [{required: true, message: '必填项不能为空', trigger: 'change'}],
          valueUser: [{required: true, message: '必填项不能为空', trigger: 'change'}],
          valueCode: [{required: true, message: '必填项不能为空', trigger: 'change'}],
          valueGroup: [{required: true, message: '必填项不能为空', trigger: 'change'}],
          valueAppUser: [{required: true, message: '必填项不能为空', trigger: 'change'}],
        },
        transferData: [], //Transfer 的数据源	array[{ key, label, disabled }]
        checked: [], //穿梭框绑定的数据，选定到右侧框中的数据项的value组成的数组
      }
    },
    beforeRouteEnter: (to, form, next) => {
      const loginType = window.sessionStorage.getItem('accountTypeLogin');
      if (loginType === '9') {
        next()
      }else{
        return next('/404')
      }
    },
    created() {
      this.getUserList();
    },
    methods: {
      async getUserList() {
        const {data: res} = await this.$http.get(`strategy/getStrategyAuthInfo/${this.queryInfo.pagenum}/${this.queryInfo.pagesize}?ts=${new Date().getTime()}`);
        if (res.code !== 0) return this.$message.error('获取信息失败！');
        this.userList = res.data.list;
        this.total = res.data.total;
      },
      handleSizeChange(newSize) {
        this.queryInfo.pagesize = newSize;
        this.getUserList()
      },
      handleCurrentChange(newPage) {
        this.queryInfo.pagenum = newPage;
        this.getUserList()
      },
      async deleById(id) {
        const confirmDelete = await this.$confirm(
          "此操作将取消授权，是否继续？",
          "提示",
          {
            confimrButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          }
        ).catch(err => err);
        if (confirmDelete !== "confirm") return;
        const {data: res} = await this.$http.post(`strategy/delStrategyAuth?strategyAuthId=${id}`);
        if (res.code !== 0) return this.$message.error(res.msg !== '' && res.msg !== null ? res.msg : '取消授权失败！');
        this.$message.success('取消授权成功！');
        this.getUserList()
      },
      changeAuthType(value) {
//        this.isShow = (value === 0) ? false : true;
      },
      formatMethod(item) {//添加
        if (item.userName !== null && item.userName.indexOf(`${this.queryValue}`) !== -1) {
          return item.userName
        } else if (item.phone !== null && item.phone.indexOf(`${this.queryValue}`) !== -1) {
          return item.phone
        } else if (item.email !== null && item.email.indexOf(`${this.queryValue}`) !== -1) {
          return item.email
        }
      },
      async remoteMethod(query) {//终端用户
        if (query !== '') {
          this.queryValue = query;
          this.loading = true;
          const {data: res} = await this.$http.get(`device/queryDeviceUser?ts=${new Date().getTime()}`, {
            params: {'deviceName': query}
          });
          if (res.code !== 0) return this.optionsUser = [];
          this.loading = false;
          this.optionsUser = res.data;
        }
      },
      async remoteMethod4(query) {//应用用户
        if (query !== '') {
          this.loading = true;
          const {data: res} = await this.$http.get(`v1/kms/queryAppUser?ts=${new Date().getTime()}`, {
            params: {'appUserName': query}
          });
          if (res.code !== 0) return this.optionsAppUser = [];
          this.loading = false;
          this.optionsAppUser = res.data;
        }
      },
      async remoteMethod2(query) {//角色
        if (query !== '') {
          this.loading = true;
          const {data: res} = await this.$http.get(`role/searchRole?ts=${new Date().getTime()}`, {
            params: {'roleCode': query}
          });
          if (res.code !== 0) return this.optionsCode = [];
          this.loading = false;
          this.optionsCode = res.data;
        }
      },
      async remoteMethod3(query) {//分组
        if (query !== '') {
          this.loading = true;
          const {data: res} = await this.$http.get(`group/queryGroupInfo?ts=${new Date().getTime()}`, {
            params: {'groupName': query}
          });
          if (res.code !== 0) return this.optionsGroup = [];
          this.loading = false;
          this.optionsGroup = res.data;
        }
      },
      openAdd() {
        this.addDialogVisible = true;
        this.getFetchData()
      },
      addDialogClosed() {
        this.optionsUser = [];
        this.optionsCode = [];
        this.addForm.valueUser = [];
        this.addForm.valueCode = [];
//        this.transferData = [];
        this.checked = [];
//        this.isShow = false;
        this.$refs.addFormRef.resetFields();
      },
      submitAdd() {
        this.$refs.addFormRef.validate(async valid => {
          if (!valid) return;
          if (this.checked.length < 1) return this.$message.error('请选择策略！');
          let reqData;
          if (this.addForm.authType === 1) {
            reqData = {
              'authType': 1,
              'deviceId': this.addForm.valueUser,
              'strategyId': this.checked
            }
          } else if (this.addForm.authType === 2) {
            reqData = {
              'authType': 2,
              'roleId': this.addForm.valueCode,
              'strategyId': this.checked
            }
          } else if (this.addForm.authType === 3) {
            reqData = {
              'authType': 3,
              'groupId': this.addForm.valueGroup,
              'strategyId': this.checked
            }
          }else if (this.addForm.authType === 4) {
            reqData = {
              'authType': 4,
              'appUserId': this.addForm.valueAppUser,
              'strategyId': this.checked
            }
          }
          const {data: res} = await this.$http.post('strategy/addStrategyAuth', reqData);
          if (res.code !== 0) return this.$message.error(res.msg !== null && res.msg !== '' ? res.msg : '添加授权失败！');
          this.addDialogVisible = false;
          this.$message.success('添加授权成功！');
          this.getUserList();
        })
      },
      async getFetchData() {
        const {data: res} = await this.$http.get(`strategy/getStrategy/0/0?ts=${new Date().getTime()}`);
        if (res.code !== 0) return this.$message.error('获取策略信息失败！');
        const allData = res.data.list;
        const data = [];
        for (let i = 0; i < allData.length; i++) {
          data.push({
            key: allData[i].strategyId,
            label: allData[i].strategyName
          });
        }
        this.transferData = data;
      },
    }
  }
</script>
<style lang="less" scoped>
  .el-select {
    display: block;
  }

  .supSpan {
    display: inline-block;
    width: 130px;
    box-sizing: border-box;
    padding-right: 12px;
    text-align: right
  }

  .supSpan:before {
    content: '*';
    color: #F56C6C;
    margin-right: 4px;
  }
</style>
